import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render select facet.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__select() {
    return (
        <div className="finder__select finder__select--level">
            <label className="finder__select__overline" htmlFor="study-level">
                Level
            </label>
            <select>
                <option>All levels</option>
                <option>Undergraduate</option>
                <option>Postgraduate</option>
            </select>
        </div>
    );
}

finder__select.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
};

export default finder__select;
